.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  transition: all 0.4s ease;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
  padding: 15px 0;
}

.navbar-scrolled {
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  padding: 10px 0;
}

.navbar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1400px;
  height: 80px;
  margin: 0 auto;
  padding: 0 30px;
}

.navbar-logo {
  display: flex;
  align-items: center;
}

.navbar-logo .logo {
  height: 220px;
  width: auto;
  filter: drop-shadow(0 0 8px rgba(255, 255, 255, 0.5));
  transition: all 0.3s ease;
}

.navbar-menu {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
  transition: all 0.3s ease;
}

.navbar-item {
  margin: 0 5px;
  position: relative;
}

.navbar-link {
  color: white;
  text-decoration: none;
  padding: 10px 15px;
  display: block;
  font-family: 'Playfair Display', serif;
  font-size: 1rem;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  transition: all 0.3s ease;
  position: relative;
}

.navbar-link::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 50%;
  background-color: #c86b52;
  transition: all 0.3s ease;
  transform: translateX(-50%);
}

.navbar-link:hover::after,
.navbar-link.active::after {
  width: 70%;
}

.navbar-link:hover,
.navbar-link.active {
  color: #f9d4c2;
}

.shopping-bag .navbar-link {
  position: relative;
  padding: 10px;
}

.bag-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart-item-count {
  position: absolute;
  top: -8px;
  right: -8px;
  background-color: #c86b52;
  color: white;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  font-size: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
}

.mobile-menu-button {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  cursor: pointer;
  z-index: 1001;
}

.mobile-menu-button span {
  display: block;
  height: 2px;
  width: 100%;
  background: white;
  border-radius: 2px;
  transition: all 0.3s ease;
}

/* Mobile responsive styles */
@media (max-width: 768px) {
  .mobile-menu-button {
    display: flex;
  }
  
  .navbar-menu {
    position: fixed;
    top: 0;
    right: -100%;
    width: 75%;
    max-width: 300px;
    height: 100vh;
    flex-direction: column;
    background: rgba(0, 0, 0, 0.9);
    backdrop-filter: blur(10px);
    padding: 80px 20px 30px;
    transition: all 0.4s ease-in-out;
    box-shadow: -5px 0 30px rgba(0, 0, 0, 0.2);
    align-items: flex-start;
  }
  
  .navbar-menu.menu-open {
    right: 0;
  }
  
  .navbar-item {
    margin: 10px 0;
    width: 100%;
  }
  
  .navbar-link {
    padding: 12px 5px;
    font-size: 1.2rem;
    width: 100%;
  }
  
  .navbar-link::after {
    bottom: 5px;
  }
  
  .shopping-bag {
    margin-top: 20px;
  }
  
  .mobile-menu-button.active span:nth-child(1) {
    transform: translateY(9px) rotate(45deg);
  }
  
  .mobile-menu-button.active span:nth-child(2) {
    opacity: 0;
  }
  
  .mobile-menu-button.active span:nth-child(3) {
    transform: translateY(-9px) rotate(-45deg);
  }
}

@media (max-width: 480px) {
  .navbar-content {
    padding: 0 15px;
  }
  
  .navbar-logo .logo {
    height: 200px;
  }
}