/* Grand Opening Banner */
.grand-opening-banner {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(45deg, #ff3b3b, #ff7b00);
  color: white;
  padding: 12px 25px;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.15);
  text-align: center;
  z-index: 10;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { transform: translateX(-50%) scale(1); }
  50% { transform: translateX(-50%) scale(1.05); }
  100% { transform: translateX(-50%) scale(1); }
}

/* Soft Launch Card */
.launch-card {
  position: absolute;
  top: 90px;
  left: 50%;
  transform: translateX(-50%);
  background: white;
  padding: 15px 20px;
  border-radius: 12px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  gap: 15px;
  width: 320px;
  text-align: center;
  z-index: 10;
  transition: all 0.3s ease-in-out;
}

.launch-card:hover {
  transform: translateX(-50%) scale(1.05);
}

.launch-image {
  flex: 1;
}

.inaugurator-img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #ff3b3b;
}

.launch-text {
  flex: 2;
}

.launch-text h3 {
  margin: 0;
  color: #333;
  font-size: 1.3rem;
  font-weight: bold;
}

.launch-text p {
  margin: 4px 0;
  color: #666;
  font-size: 1rem;
}

/* Responsive */
@media (max-width: 768px) {
  .grand-opening-banner {
    font-size: 1rem;
    padding: 10px 20px;
  }

  .launch-card {
    flex-direction: column;
    align-items: center;
    width: 90%;
  }

  .inaugurator-img {
    width: 70px;
    height: 70px;
  }

  .launch-text h3 {
    font-size: 1.1rem;
  }

  .launch-text p {
    font-size: 0.9rem;
  }
}


