/* Full-Screen Background */
.softlaunch-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to right, #fbc2a6, #ffedd5);
  color: white;
  text-align: center;
  font-family: "Poppins", sans-serif;
  position: relative;
  overflow: hidden;
}

/* Background Overlay */
.background-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(6px);
}

/* Floating Glass Invitation Card */
.invitation-card {
  width: 90%;
  max-width: 900px;
  background: rgba(255, 235, 219, 0.35);
  border-radius: 20px;
  padding: 40px;
  backdrop-filter: blur(20px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
  position: relative;
  z-index: 2;
}

/* Content Layout */
.content-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

/* Event Details */
.event-details {
  flex: 1;
  text-align: left;
  padding: 20px;
}

.event-title {
  font-size: 2.5rem;
  font-weight: bold;
  background: linear-gradient(90deg, #d97652, #ff8c66);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.event-subtitle {
  font-size: 1.2rem;
  margin-top: 10px;
  opacity: 0.8;
  color: #6b4226;
}

.event-info p {
  font-size: 1.1rem;
  margin: 8px 0;
  color: #6b4226;
}

/* Chief Guest Section */
.chief-guest {
  text-align: center;
  flex: 0.5;
}

.guest-img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  border: 4px solid #d97652;
  object-fit: cover;
  margin-bottom: 12px;
}

/* Animated Confetti */
.confetti-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.confetti {
  width: 10px;
  height: 10px;
  background-color: gold;
  position: absolute;
  opacity: 0.7;
  transform: rotate(45deg);
  animation: confetti-fall 3s infinite ease-in-out;
}

@keyframes confetti-fall {
  0% {
    transform: translateY(-10vh) rotate(0deg);
  }
  100% {
    transform: translateY(100vh) rotate(720deg);
  }
}

/* Confetti Position */
.confetti:nth-child(odd) {
  background-color: peachpuff;
}
.confetti:nth-child(even) {
  background-color: gold;
}
.confetti:nth-child(3n) {
  background-color: coral;
}

/* Responsive Fixes */
@media (max-width: 768px) {
  .content-wrapper {
    flex-direction: column;
    text-align: center;
  }

  .event-details {
    text-align: center;
  }

  .guest-img {
    width: 130px;
    height: 130px;
  }

  .invitation-card {
    padding: 30px;
  }
}
